body, p{
    font-size: 16px;
    line-height: 1.2rem;
}
body {
    font-family: 'Lato', sans-serif;
}
.setFont{
    font-family: 'Lato', sans-serif;
}
.tile-icon {
    font-size: 125px;
}

.social-wrapper .tile-icon {
    font-size: 20px;
}

.perf-wrapper>ul {
    font-size: 1rem;
}

.perf-wrapper>table, .total-ques-wrapper table {
    line-height: 1.2rem;
    font-size: 1rem;
}

.field-wrapper textarea, .field-wrapper input {
    font-size: 14px;
}

.contact-wrapper .icon {
    font-size: 35px;
}


.banner .type {
    font-size: 1.6em;
}

.banner .link {
    font-size: 2em;
}

.project .badges span {
    font-size: 0.8em;
    line-height: 1.5em;
}

.skill .icon {
    font-size: 30px;
}

.tile-icon {
    font-size: 25px;
}
.social-wrapper .tile-icon {
    font-size: 20px;
}
.social-wrapper .tile {
    font-size: 20px;
}

@media only screen and (min-width: 1000px) {
    .no-window .tile-icon {
        font-size: 30px;
    }
    .no-window .social-wrapper .tile-icon {
        font-size: 25px;
    }
}