/* According to Height */
@media only screen and (max-height: 1300px) {
    .wrapper {
        padding: 15px;
        margin: 15px;
        height: calc(100vh - 30px);
        gap: 15px;
    }
    .grid-wrapper{
        gap: 15px;
    }
}
@media only screen and (max-height: 620px) {
    .wrapper {
        padding: 15px;
        margin: 15px;
        height: calc(100vh - 30px);
        gap: 15px;
    }
    .grid-wrapper{
        gap: 15px;
    }

}
@media only screen and (max-height: 550px) {
    .wrapper {
        padding: 10px;
        margin: 10px;
        height: calc(100vh - 20px);
        gap: 10px;
        /* grid-template-columns: 1fr 0fr; */
    }
    .grid-wrapper{
        gap: 10px;
    }

}

@media only screen and (max-height: 500px) {
    .wrapper:not(.no-window) {
        padding: 10px!important;
        margin: 10px!important;
        height: calc(100vh - 20px)!important;
        gap: 10px;
    }
    .grid-wrapper{
        gap:10px;
    }

}
@media only screen and (max-height: 450px) {
    .ReactModal__Content {
        inset: 10px 10px!important;
    }
    .grid-wrapper{
        grid-template-areas: 
            'op  hd  hd  hd hd cp'
            'op  ab  pr  pr sk cp'
            'ed  ed  ex  ex cr so'
            'ac  ec  ex  ex re co';
    }
    .wrapper{
        padding: 15px!important;
        margin: 15px!important;
        height: calc(100vh - 30px)!important;
        grid-template-columns: 1fr 0fr;
        gap: 0px
    }
    .grid-wrapper{
        gap: 15px!important;
    }
}
