@media only screen and (max-width: 340px) {
    
    .wrapper {
        padding: 10px;
    }
    .tile {
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .grid-wrapper {
        grid-template-areas: 'hd  hd' 'ab  pr' 'ed  sk' 'ex  ex' 'cp  op' 'cp  op' 'ac  ec' 're  cr' 'co  so';
    }
}

@media only screen and (max-width: 250px) {
    .wrapper {
        padding: 5px;
        margin: 5px;
        /*min-*/height: calc(100vh - 10px);
    }
    .tile {
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .grid-wrapper {
        grid-template-areas: 'hd  hd' 'ab  pr' 'ed  sk' 'ex  ex' 'cp  op' 'cp  op' 'ac  ec' 're  cr' 'co  so';
        gap: 5px;
    }
    .social-wrapper {
        gap: 5px;
    }
}

@media only screen and (max-width: 270px) {
    .grid-wrapper {
        grid-template-areas: 'hd' 'ab '' pr' 'ed '' sk' 'ex '' ex' 'cp '' op' 'ac '' ec' 're '' cr' 'co '' so';
        gap: 5px;
    }
    .social-wrapper .tile{
        padding: 15px;
    }
}