.grid-wrapper {
    display: grid;
    width: 100%;
    gap: 20px;
    color: #181815;;
}

.grid-wrapper{
    grid-template-areas: 
        'hd  hd  hd  hd'
        'ab  pr  pr  sk'
        'ed  ed  ex  ex'
        'ac  cr  ex  ex'
        'cp  cp  op  op' 
        'ec  re  so  co'
}
@media only screen and (min-width: 850px) {
    .grid-wrapper.no-window{
        grid-template-areas: 
            'op  hd  hd  hd hd cp'
            'op  ab  pr  pr sk cp'
            'ed  ed  ex  ex cr so'
            'ac  ec  ex  ex re co';
        gap: 1.5em
        };
}

@media only screen and (max-width: 500px) {
    .grid-wrapper{
        grid-template-areas: 
        'hd  hd  hd'
        'ab  pr  pr'
        'ed  ed  sk'
        'cp  cp  ex' 
        'op  op  ex'
        'ac  re  cr'
        'ec  co  so';
    }
}


.tile {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 1em;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;
    user-select: none;
    cursor: pointer;
    padding: 10px 5px;
    text-align: center;
    text-decoration: none;
    color: inherit;
}




.grid-wrapper .active {
    background-color: #027aff;
    color: white;
}

.tile.small {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}



.social-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 0.5em
}

.social-wrapper .tile {
    min-width: auto!important;
    width: auto!important;
    padding: 0px 15px;
}

@media only screen and (min-width: 1000px) {
    .tile {
        min-width: 6vw;
    }
    .no-window .tile {
        min-width: 15vw;
    }
    .no-window .social-wrapper {
        gap: 1em
    }
}