.shadow {
    -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.185);
    -moz-box-shadow: 0 0 20px rgba(0, 0, 0, 0.185);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.185);
}

.wrapper, .no-window.wrapper.animate-window {
    background-color: rgba(255, 255, 255, 0.2);
    display: grid;
    grid-template-columns: max-content 1fr;
    gap: 20px;
    border-radius: 1rem;
    width: auto;
    margin: 6vh;
    /*min-*/height: 88vh;
    padding: 20px;
    overflow: auto;
}
.wrapper.no-window {
    gap: 0px;
}

.layout-wrapper {
    width: auto;
    height: 100%;
}

.window-wrapper {
    overflow: hidden;
}

/* No window Opened */

@media only screen and (min-width: 1000px) {
    .wrapper.no-window, .wrapper.animate-window {
        grid-template-columns: 1fr 0fr;
        margin: 10vh;
        /*min-*/height: 80vh;
        gap: 0;
    }
}

/* --- */

@media only screen and (max-width: 1000px) {
    .wrapper {
        padding: 20px;
        margin: 4vh 3vw;
        /*min-*/height: 92vh;
        grid-template-columns: 1fr 1fr;
    }
    .wrapper.no-window, .wrapper.animate-window {
        grid-template-columns: 1fr 0fr;
    }
}

@media only screen and (max-width: 850px) {
    .wrapper {
        padding: 20px;
        margin: 4vh 3vw;
        /*min-*/height: 92vh;
        grid-template-columns: 1fr 0fr;
        gap: 0!important;
    }
    .window-wrapper {
        display: none;
    }
}